import React from "react"
import ListGroup from "react-bootstrap/ListGroup"

const ThemeItem = ({ name, id, onSelect, active = false }) => {
  return (
    <ListGroup.Item action active={active} variant="info" style={{ margin: "5px 0" }}
                    onClick={() => {
                      onSelect(+id);
                    }}>
      {name}
    </ListGroup.Item>
  )
}

export default ThemeItem
