import React, { useState } from "react"
import ListGroup from "react-bootstrap/ListGroup"
import ThemeItem from "./theme-item/theme-item"
import { useStaticQuery, graphql } from "gatsby"
import Button from "react-bootstrap/Button"
import { navigate } from "gatsby"
import { shuffle, shuffle2n } from "../../utils/functions"

const SelectEnglishThemes = ({ mode }) => {
  // if mode 'learn' => no shuffle in makeArrayOfWords
  const data = useStaticQuery(graphql`{
 allJson {
    nodes {
      label
      id
      values
    }
  }
}
`)
  let localStorageData
  if (typeof window !== "undefined") {
    localStorageData = localStorage.getItem("favorites")
  }
  const wordsFromBase = (JSON.parse(JSON.stringify(data.allJson.nodes)).reverse())
  if (localStorageData) {
    wordsFromBase.unshift(JSON.parse(localStorageData))
  }
  const [selected, setSelected] = useState([])
  const [start, startActive] = useState(true)
  const [nextPagDsbl, setNextPagDsbl] = useState(false)
  const [prevPagDsbl, setPrevPagDsbl] = useState(false)
  const [currentPage, setCurrentPage] = useState(1)
  const [postsPerPage] = useState(6)


  const onSelect = (id) => {
    const arr = selected.slice()
    const check = arr.indexOf(+id)
    if (check === -1) {
      arr.push(+id)
    } else {
      arr.splice(check, 1)
    }
    setSelected(arr)
  }
  if (selected.length > 0) {
    if (start !== false)
      startActive(false)
  } else {
    if (start !== true)
      startActive(true)
  }
  const makeArrayOfWords = (obj, selected) => {
    const arr = selected.reduce((prev, el) => {
      const idx = obj.findIndex((item) => +item.id === el)
      return prev.concat(obj[idx].values)
    }, [])
    return arr
  }

  const makeTestOfWords = (obj, selected) => {
    const testArr = shuffle2n(makeArrayOfWords(obj, selected))
    let helpArr = [];
    if (testArr.length < 4) {
      //создает массив из случайных тем кроме выбранных
      const helpArrRandom = shuffle(Array(obj.length).fill(0).map((el, n) => n).filter(el => !selected.includes(el)))

      let count = 0
      while (helpArr.length < 4 && count < 10) {
        if (helpArrRandom.length) {
          if (helpArr.length) {
            obj[helpArrRandom.pop()].values.forEach(el=>helpArr.push(el));
          } else {
            helpArr = makeArrayOfWords(obj, selected)
          }
        } else {
          console.log("not enough words")
          return;
        }
        count++;
      }
    }
    const makeVariants = (word, testArr, helpArr) => {
      let arr;
      if (testArr.length < 4) {
        arr = helpArr.filter(el=> el[0]!==word[0])
      } else {
        arr = testArr.filter(el=> el[0]!==word[0])
      }
      arr = shuffle2n(arr);
      arr = arr.splice(0,3);
      arr.push(word);
      arr = shuffle2n(arr);
      return arr;
    }
    const words = testArr.map(el=>{
      return {
        question: el,
        variants: makeVariants(el, testArr, helpArr),
      }
    });
    return words;
  }
  const isActive = (id) => {
    return selected.indexOf(+id) !== -1
  }
  const indexOfLastPost = currentPage * postsPerPage
  const indexOfFirstPost = indexOfLastPost - postsPerPage
  const currentThemes = wordsFromBase.slice(indexOfFirstPost, indexOfLastPost)
  if (currentPage === 1) {
    if (!prevPagDsbl) {
      setPrevPagDsbl(true)
    }
  } else {
    if (prevPagDsbl) {
      setPrevPagDsbl(false)
    }
  }
  const lastPage = Math.ceil(wordsFromBase.length / postsPerPage)
  if (currentPage === lastPage) {
    if (!nextPagDsbl) {
      setNextPagDsbl(true)
    }
  } else {
    if (nextPagDsbl) {
      setNextPagDsbl(false)
    }
  }
  // Change page
  const paginateUp = () => setCurrentPage(currentPage + 1)
  const paginateDown = () => setCurrentPage(currentPage - 1)

  //start button click => navigation
  const onStartClick = () => {
    let navigation
    if (mode === "learn") {
      navigation = `/learn/learn`
      navigate(navigation, {
        state: { words: makeArrayOfWords(wordsFromBase, selected) },
      })
    } else if (mode === "translate") {
      navigation = `/translate/translate`
      navigate(navigation, {
        state: { words: shuffle2n(makeArrayOfWords(wordsFromBase, selected)) },
      })
    } else if (mode === "test") {
      navigation = `/test/test`;
      navigate(navigation, {
        state: { wordsData: makeTestOfWords(wordsFromBase,selected) },
      })
      makeTestOfWords(wordsFromBase, selected);
    }

  }
  return (<>
      <h1 className="text-center bg-light">Select themes</h1>
      <div className="select-wrapper">
        <ListGroup style={{ width: "90%", minWidth: "250px" }}>
          {currentThemes.map((item) => {
            return <ThemeItem name={item.label} onSelect={onSelect}
                              active={isActive(item.id)} id={item.id} key={item.id}/>
          })}

        </ListGroup>
      </div>
      <div className="flex-column align-content-center">
        <div className="row m-0">
          <Button className="col mr-2 btn btn-info my-2" disabled={prevPagDsbl} onClick={paginateDown}>←</Button>
          <div className="mr-2 bg-light my-2 d-flex justify-content-center align-items-center"
               style={{ width: "50px" }}>{currentPage}</div>
          <Button className="col btn btn-info my-2" disabled={nextPagDsbl} onClick={paginateUp}>→</Button>
        </div>
        <Button variant="success" disabled={start} className="col btn btn-success mb-2"
                onClick={onStartClick}>Start</Button>
      </div>
    </>
  )
}
export default SelectEnglishThemes
